import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { loadCourses, saveCourse } from './../../redux/actions/courseAction';
import { loadAuthors } from './../../redux/actions/authorAction';
import PropTypes from 'prop-types';
import CourseForm from './CourseForm';
import { newCourse } from './../../../tools/mockData';
import Spinner from './../common/Spinner';
import { toast } from 'react-toastify';

export function ManageCoursePage({ courses, authors, loadCourses, loadAuthors, saveCourse, history, ...props }) {
	const [course, setCourse] = useState({ ...props.course });
	const [errors, setErrors] = useState({});
	const [saving, setSaving] = useState(false);

	useEffect(
		() => {
			if (courses.length === 0) {
				loadCourses().catch((error) => {
					alert(`Loading ManageCourse failed ${error}`);
				});
			} else {
				setCourse({ ...props.course });
			}

			if (authors.length === 0) {
				loadAuthors().catch((error) => {
					alert(`Loading authors failed ${error}`);
				});
			}
		},
		[props.course]
	);

	function handleChange(event) {
		const { name, value } = event.target;
		setCourse((preCourse) => ({
			...preCourse,
			[name]: name === 'authorId' ? parseInt(value, 10) : value
		}));
	}

	function formIsValidate() {
		const { title, authorId, category } = course;
		const errors = {};

		if (!title) errors.title = 'Title is required.';
		if (!authorId) errors.author = 'Author is required.';
		if (!category) errors.category = 'Category is required.';

		setErrors(errors);
		return Object.keys(errors).length === 0;
	}
	function handleSaveCourse(event) {
		event.preventDefault();
		if (!formIsValidate()) return;

		setSaving(true);
		saveCourse(course)
			.then(() => {
				toast.success('Course Saved!');
				history.push('/courses/');
			})
			.catch((error) => {
				setSaving(false);
				setErrors({ onSave: error.message });
			});
	}

	return (
		<div>
			{courses.length === 0 || authors.length === 0 ? (
				<Spinner />
			) : (
				<CourseForm
					course={course}
					authors={authors}
					errors={errors}
					onChange={handleChange}
					onSave={handleSaveCourse}
					saving={saving}
				/>
			)}
		</div>
	);
}

ManageCoursePage.propTypes = {
	course: PropTypes.object,
	courses: PropTypes.array.isRequired,
	authors: PropTypes.array.isRequired,
	loadAuthors: PropTypes.func.isRequired,
	loadCourses: PropTypes.func.isRequired,
	saveCourse: PropTypes.func.isRequired,
	history: PropTypes.any.isRequired
};

export function getCourseBySlug(courses, slug) {
	return courses.find((course) => course.slug === slug) || null;
}

function mapStateToProps(state, ownProps) {
	const slug = ownProps.match.params.slug;
	const course = slug && state.courses.length > 0 ? getCourseBySlug(state.courses, slug) : newCourse;
	return {
		course,
		courses: state.courses,
		authors: state.authors
	};
}

// user mapDispatchToProps as object
const mapDispatchToProps = {
	loadCourses,
	loadAuthors,
	saveCourse
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageCoursePage);
